	module.exports = {"data":{"nodePost":{"title":"My Re-Retirement From Comedy","body":{"value":"<p>If the photo above of me forcing Evan to perform much of the show as a talking broom didn't make it clear, being back at ImprovBoston for the 10th anniversary of Harold Night was a blast. It was strange being back in the building at first, but once I set foot on stage it felt as if no time had passed at all. And I even had an opportunity to publicly shame my brother for his eating habits, which makes the night a success by any measure.</p> \r\n<p>Thanks to all who took in this over the top show, avoided my flailing limbs when I repeatedly climbed over audience chairs, and humored a now dad-heavy group of improvisers as they gave comedy one more spin. Being able to see so many great friends that I hadn't seen in years makes getting stuck on the red line for an hour more than worth it.</p>\r\n<p>The show was such a success that I'll once again be retiring from improv effective immediately. I'll see you all at the 20 year reunion.</p>\r\n<p>(Photo via <a href=\"https://twitter.com/haroldnight\">@haroldnight</a>. Another <a href=\"https://www.facebook.com/pg/haroldnight/photos/?tab=album&album_id=10156702164471535\">great photo set</a> is available on the Harold Night Facebook page.)</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":1.3333333333333333,"src":"/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-42807.jpg","srcSet":"/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-960f9.jpg 313w,\n/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-c36f2.jpg 625w,\n/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-42807.jpg 1250w,\n/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-dbc5e.jpg 1875w,\n/static/ac6b8c38f60653e93466f40d289d9e3a-e7e1cd8e1feccc200e98f9ff771c6717-36d9f.jpg 2048w","sizes":"(max-width: 1250px) 100vw, 1250px"}}}}},"created":1525713992}},"pathContext":{"slug":"/post/my-re-retirement-from-comedy/"}}

